import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PurchaseReturnRoutingModule } from './purchase-return-routing.module';
import { PurchaseReturnComponent } from './purchase-return.component';
import { SharedModule } from '../../../shared/modules/shared.module';
import { PurchaseReturnAddComponent } from './purchase-return-add/purchase-return-add.component';
import { PurchaseReturnViewComponent } from './purchase-return-view/purchase-return-view.component';
import { PurchaseReturnService } from '../../../shared/services/purchase-return.service';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    PurchaseReturnRoutingModule
  ],
  providers: [
    PurchaseReturnService
  ],
  declarations: [PurchaseReturnComponent, PurchaseReturnAddComponent, PurchaseReturnViewComponent],
  entryComponents: [
    PurchaseReturnAddComponent,
    PurchaseReturnViewComponent
  ]
})
export class PurchaseReturnModule { }



