import { Component, OnInit, EventEmitter, Output, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { ItemMasterService, AllReportService, ErrorHandleService } from '../../../../shared';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { PurchaseReturnService } from '../../../../shared/services/purchase-return.service';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-purchase-return-add',
  templateUrl: './purchase-return-add.component.html',
  styleUrls: ['./purchase-return-add.component.scss']
})
export class PurchaseReturnAddComponent implements OnInit {

  @Output() closeWindow = new EventEmitter();
  @Output() updateGrid = new EventEmitter();
  @ViewChild('grnComboBox') grnComboBox: jqxComboBoxComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  purchaseReturnForm: FormGroup;
  purchaseItemFocus: any = [];
  sentItemAdapter: any = [];
  grnAdapter: any = [];

  grn: boolean = false;

  constructor(
    private fb: FormBuilder,
    private ims: ItemMasterService,
    private rs: AllReportService,
    private prs: PurchaseReturnService,
    private ehs: ErrorHandleService,
  ) { }

  ngOnInit() {
    this.purchaseReturnForm = this.fb.group({
      date: [null, Validators.required],
      purchaseReturnItems: this.fb.array([
        this.initMaterials()
      ])
    })

    this.rs.getAllGrn({ limit: 'all' }).subscribe(
      response => {
        this.grnAdapter = response;
      },
      error => {
        console.log(error);
      }
    )

  }

  private initMaterials() {
    return this.fb.group({
      item_id: [null, Validators.required],
      item_name: [null, Validators.required],
      quantity: [null, Validators.required],
      rate: [null, Validators.required],
      value: [null]
    })
  }

  addItem(items) {
    const control1 = <FormArray>this.purchaseReturnForm.controls['purchaseReturnItems'];
    control1.push(this.initMaterials());
  }

  removeItem(i: number, items) {
    const control1 = <FormArray>this.purchaseReturnForm.controls['purchaseReturnItems'];
    control1.removeAt(i);
  }

  purchaseItemListSelected(selectedEvent, index) {
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      let displayText = selectedEvent.target.selectedOptions[0].text;
      this.purchaseReturnForm.get('purchaseReturnItems')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
      this.purchaseReturnForm.get('purchaseReturnItems')['controls'][index].get('item_name').patchValue(displayText);
      this.purchaseItemFocus[index] = false;
    }
  }

  purchaseItemFilter(searchPr, index) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode2').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 1) {
      if (temp) {
        this.purchaseItemFocus[index] = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            this.sentItemAdapter[index] = [];
            this.sentItemAdapter[index] = response;
          },
          error => {
            console.log(error);
          }
        );

      } else {
        this.purchaseItemFocus[index] = false;
      }
    }
  }

  save(value) {
    this.jqxLoader.open();
    value.grn_id = this.grnComboBox.val();
    this.prs.store(value).subscribe(
      result => {
        this.jqxLoader.close();
        this.updateGrid.emit();
      }, error => {
        this.jqxLoader.close();
        let data = this.ehs.formatError(error);
        let messageDiv: any = document.getElementById('windowMessage');
        messageDiv.innerText = data['message'];
        this.winNotification.open();
        this.jqxLoader.close();
      }
    )
  }

  cancelBtn() {
    this.closeWindow.emit();
  }

  calculateValue($event, i) {
    const quantity = this.purchaseReturnForm.get('purchaseReturnItems')['controls'][i].get('quantity').value;
    const rate = this.purchaseReturnForm.get('purchaseReturnItems')['controls'][i].get('rate').value;

    if (quantity && rate) {
      let total = parseFloat(quantity) * parseFloat(rate);
      this.purchaseReturnForm.get('purchaseReturnItems')['controls'][i].get('value').patchValue(total);
    }
  }

  grnChanged() {
    this.grn = this.grnComboBox.val() ? true : false;
  }

}



